* {
  font-family: 'Montserrat', sans-serif;
  color: #111; }

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; }

.App .title {
  margin: 4rem auto; }

.App button {
  margin: 0 .2rem;
  padding: 1rem;
  border: 2px solid transparent;
  border-radius: .5rem;
  background-color: paleturquoise;
  color: #111; }

text
.App button.active {
  border: 2px solid darkcyan; }

.App .track-feed {
  font-size: 1.5rem; }

.App .App-mode {
  align-self: flex-end;
  margin-top: 3px; }

.App .App-content {
  display: flex;
  flex-direction: column; }
